.rnBackDrop {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 100000;
  background-color: rgba(0, 0, 0, 0.5); }
  .rnBackDrop.rnFade {
    opacity: 0; }
  .rnBackDrop.rnShow {
    opacity: .5; }

.rnModal {
  width: 500px;
  position: fixed;
  top: 0;
  left: 50%;
  background-color: white;
  z-index: 100001;
  border-radius: 5px; }

.rnModal .rnCloseIcon {
  cursor: pointer; }

.rnModal .rnCloseIcon:hover {
  color: red; }

.rnHeader {
  padding: 16px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  align-items: center; }

.rnFooter {
  padding: 16px;
  text-align: right; }

.rnFooter button {
  margin-left: 5px; }

.rnContent {
  padding: 16px;
  border-bottom: 1px solid #e9ecef;
  border-top: 1px solid #e9ecef; }
